export default {
	props:{
		isLast:false,
		cardObj:Object,
		newsList:{
			type: Array,
			default(){
				return [];
			}
		},
		showBar:{
			type:Boolean,
			default:true
		}
	},
	data() {
		return {
			isCard: true,
			myList:[]
			
		};
	},
	onLoad() {
	},
	methods: {
	
		moreNews(data){
			var cardObjStr = JSON.stringify(data)
			uni.navigateTo({
				url:'../../pages/news/MoreNews/MoreNews?cardObjStr='+cardObjStr
			})
		},
		goToNewsDetail(data){
			// var htmlContent = data.htmlContent
			// console.log(htmlContent)
			// data.htmlContent=""
			// var newsStr = JSON.stringify(data)
			// uni.navigateTo({
			// 	url:'../../pages/news/NewsDetail?newsStr='+newsStr+'&htmlContent='+htmlContent
			// })
			uni.navigateTo({
				url:'../../pages/news/NewsDetail?newsStr='+encodeURIComponent(JSON.stringify(data))
			})
			
		},
		IsCard(e) {
			this.isCard = e.detail.value
		},
	}
}



